"use client"

import { useState, useEffect } from "react"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { BarChart } from "@/components/ui/bar-chart"

interface Progress {
  student_id: number;
  course_id: number;
  progress_percentage: number;
  last_accessed: string;
  course_title: string;
  student_name: string;
  total_assignments: number;
  completed_assignments: number;
}

interface Trend {
  month: string;
  avg_progress: number;
  active_students: number;
}

interface ProgressData {
  progress: Progress[];
  stats: {
    avg_progress: number;
  };
  trends: Trend[];
}

export default function ProgressPage() {
  const [data, setData] = useState<ProgressData | null>(null)
  const [loading, setLoading] = useState(true)

  const fetchProgress = async () => {
    try {
      const response = await fetch('/api/progress')
      if (!response.ok) {
        throw new Error('Failed to fetch progress data')
      }
      const data = await response.json()
      setData(data)
    } catch (error) {
      console.error('Error fetching progress:', error)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    fetchProgress()
  }, [])

  if (loading) {
    return (
      <div className="p-8">
        <div className="animate-pulse">
          <div className="h-8 bg-gray-200 rounded w-1/4 mb-4"></div>
          <div className="h-4 bg-gray-200 rounded w-1/3 mb-8"></div>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
            {[1, 2, 3].map((i) => (
              <Card key={i} className="p-6">
                <div className="h-6 bg-gray-200 rounded w-1/3 mb-2"></div>
                <div className="h-8 bg-gray-200 rounded w-1/2"></div>
              </Card>
            ))}
          </div>
          <Card className="p-6 mb-8">
            <div className="h-[400px] bg-gray-100 rounded"></div>
          </Card>
        </div>
      </div>
    )
  }

  if (!data) {
    return (
      <div className="p-8">
        <Card className="p-12 text-center">
          <p className="text-lg text-gray-500">Failed to load progress data</p>
        </Card>
      </div>
    )
  }

  const chartData = {
    labels: data.trends.map(trend => trend.month),
    datasets: [
      {
        label: 'Average Progress',
        data: data.trends.map(trend => trend.avg_progress),
        backgroundColor: 'rgba(59, 130, 246, 0.5)',
        yAxisID: 'progress'
      },
      {
        label: 'Active Students',
        data: data.trends.map(trend => trend.active_students),
        backgroundColor: 'rgba(34, 197, 94, 0.5)',
        yAxisID: 'students'
      }
    ]
  }

  const chartOptions = {
    responsive: true,
    interaction: {
      mode: 'index' as const,
      intersect: false,
    },
    scales: {
      progress: {
        type: 'linear' as const,
        display: true,
        position: 'left' as const,
        title: {
          display: true,
          text: 'Average Progress (%)'
        }
      },
      students: {
        type: 'linear' as const,
        display: true,
        position: 'right' as const,
        title: {
          display: true,
          text: 'Active Students'
        },
        grid: {
          drawOnChartArea: false,
        },
      },
    },
  }

  return (
    <div className="p-8">
      <div className="mb-8">
        <h1 className="text-2xl font-bold mb-2">Course Progress</h1>
        <p className="text-gray-600">Track your learning progress across all courses</p>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Average Progress</h3>
          <p className="text-3xl font-bold">{Math.round(data.stats.avg_progress)}%</p>
        </Card>

        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Active Courses</h3>
          <p className="text-3xl font-bold">{data.progress.length}</p>
        </Card>

        <Card className="p-6">
          <h3 className="text-sm font-medium text-gray-500 mb-1">Completed Assignments</h3>
          <p className="text-3xl font-bold">
            {data.progress.reduce((acc, p) => acc + p.completed_assignments, 0)}
          </p>
        </Card>
      </div>

      <Card className="p-6 mb-8">
        <h2 className="text-lg font-semibold mb-4">Progress Trends</h2>
        <div className="h-[400px]">
          <BarChart data={chartData} options={chartOptions} />
        </div>
      </Card>

      <div className="space-y-6">
        <h2 className="text-lg font-semibold">Course Progress Details</h2>
        {data.progress.map((item) => (
          <Card key={`${item.course_id}-${item.student_id}`} className="p-6">
            <div className="flex items-center justify-between mb-4">
              <div>
                <h3 className="font-semibold">{item.course_title}</h3>
                <p className="text-sm text-gray-500">
                  Last accessed: {new Date(item.last_accessed).toLocaleDateString()}
                </p>
              </div>
              <div className="text-right">
                <p className="text-2xl font-bold">{Math.round(item.progress_percentage)}%</p>
                <p className="text-sm text-gray-500">
                  {item.completed_assignments} of {item.total_assignments} assignments completed
                </p>
              </div>
            </div>

            <div className="w-full bg-gray-200 rounded-full h-2.5">
              <div
                className="bg-blue-600 h-2.5 rounded-full"
                style={{ width: `${item.progress_percentage}%` }}
              />
            </div>

            <div className="mt-4 flex justify-end">
              <Button variant="outline">View Course</Button>
            </div>
          </Card>
        ))}
      </div>

      {data.progress.length === 0 && (
        <Card className="p-12 text-center">
          <p className="text-lg text-gray-500">No courses in progress</p>
          <Button className="mt-4">Browse Courses</Button>
        </Card>
      )}
    </div>
  )
}
