"use client"

import { useState, useEffect } from "react"
import { Bar } from "react-chartjs-2"
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
} from "chart.js"

// Register Chart.js components once
ChartJS.register(
  CategoryScale,
  LinearScale,
  BarElement,
  Title,
  Tooltip,
  Legend
)

interface BarChartProps {
  data: {
    labels: string[]
    datasets: {
      label: string
      data: number[]
      backgroundColor: string
      yAxisID: string
    }[]
  }
  options?: any
}

export function BarChart({ data, options }: BarChartProps) {
  const [mounted, setMounted] = useState(false)

  // Set mounted to true once the component mounts (client-side only)
  useEffect(() => {
    setMounted(true)
  }, [])

  if (!mounted) {
    // Render a loading skeleton that matches SSR output
    return <div className="w-full h-full bg-gray-100 rounded-lg animate-pulse" />
  }

  return (
    <div className="w-full h-full">
      <Bar data={data} options={options} />
    </div>
  )
}
